<template>
  <div class="Competitions">
    <div class="main-panel">
      <div v-for="[group, list], index in Object.entries(competitionGroups)" :key="index">
        <CompetitionList :list="list" :title="timeFormat(group)"/>
      </div>
    </div>
  </div>
</template>

<script>
import CompetitionList from "@/components/CompetitionList.vue";
import { getList } from "../api/competitions";

export default {
  name: "Competitions",
  components: {
    CompetitionList,
  },
  data() {
    return {
      competitionGroups: {}
    }
  },
  created() {
    getList().then((data) => {
      this.competitionGroups = this.groupCompetitions(data);
    });
  },
  methods: {
    timeFormat(Y_M) {
      console.log(Y_M);
      const y_m = Y_M.split('-')
      return `${y_m[0]} 年 ${y_m[1]} 月`
    },
    groupCompetitions(competitions) {
      let groups = {};

      function safeAdd(key, value) {
        if (groups[key]) {
          groups[key].push(value);
        } else groups[key] = [value];
      }

      if (competitions && competitions.length > 0) {
        competitions.map((comp) => {
          safeAdd(comp.signUpTimeFrom.slice(0, 7), comp)
        });
      }

      return groups
    },
  },
};
</script>

<style scoped>
.main-panel {
  background-color: #f8f9fb;
  padding: 20px;
}
</style>